import React, { Component } from "react";
import Logo2 from "../assets/img/logo.png";

class home extends Component {
  render() {
    return (
      <>
        <div className="container row" style={{ marginBottom: "130px" }}>
          <div>
            <img src={Logo2} alt="logo" width="100%" />
          </div>
        </div>
      </>
    );
  }
}

export default home;
