import React, { Component } from "react";
import Logo from "../assets/icon/logo.png";
import { Link } from "react-router-dom";

class footer extends Component {
  render() {
    return (
      <footer className="page-footer blue">
        <div className="container">
          <div className="row">
            <div className="col l3 m3 s12">
              <img src={Logo} alt="logo" height="200px" />
            </div>
            <div className="col l9 m9 s12">
              <ul className="hide-on-med-and-up">
                <li>
                  <Link
                    to="/"
                    className="grey-text text-lighten-3"
                    style={{ textDecoration: "underline", fontSize: "1.2em" }}
                  >
                    Home
                  </Link>
                </li>
                <li>
                  <Link
                    to="/promo"
                    className="grey-text text-lighten-3"
                    style={{ textDecoration: "underline", fontSize: "1.2em" }}
                  >
                    Hot Promo
                  </Link>
                </li>
                <li>
                  <Link
                    to="/tracker"
                    className="grey-text text-lighten-3"
                    style={{ textDecoration: "underline", fontSize: "1.2em" }}
                  >
                    Tracker
                  </Link>
                </li>
                <li>
                  <Link
                    to="/tentang"
                    className="grey-text text-lighten-3"
                    style={{ textDecoration: "underline", fontSize: "1.2em" }}
                  >
                    Tentang Kami
                  </Link>
                </li>
                <li>
                  <Link
                    to="/kontak"
                    className="grey-text text-lighten-3"
                    style={{ textDecoration: "underline", fontSize: "1.2em" }}
                  >
                    Kontak
                  </Link>
                </li>
              </ul>
              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  justifyContent: "space-between",
                }}
                className="hide-on-small-only"
              >
                <Link
                  to="/"
                  className="grey-text text-lighten-3"
                  style={{ textDecoration: "underline", fontSize: "1.2em" }}
                >
                  Home
                </Link>
                <Link
                  to="/promo"
                  className="grey-text text-lighten-3"
                  style={{ textDecoration: "underline", fontSize: "1.2em" }}
                >
                  Hot Promo
                </Link>
                <Link
                  to="/tracker"
                  className="grey-text text-lighten-3"
                  style={{ textDecoration: "underline", fontSize: "1.2em" }}
                >
                  Tracker
                </Link>
                <Link
                  to="/tentang"
                  className="grey-text text-lighten-3"
                  style={{ textDecoration: "underline", fontSize: "1.2em" }}
                >
                  Tentang Kami
                </Link>
                <Link
                  to="/kontak"
                  className="grey-text text-lighten-3"
                  style={{ textDecoration: "underline", fontSize: "1.2em" }}
                >
                  Kontak
                </Link>
              </div>
              <div
                style={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                  marginTop: "50px",
                }}
              >
                <div style={{ display: "flex", justifyContent: "center" }}>
                  Follow kami di :
                </div>
                <div
                  style={{
                    display: "flex",
                    justifyContent: "space-around",
                    marginTop: "30px",
                    width: "100%",
                  }}
                >
                  <div style={{ display: "flex", alignItems: "center" }}>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        backgroundColor: "rgba(0,0,0,0)",
                        border: "1px solid white",
                        width: "20px",
                        height: "20px",
                        padding: "15px",
                        borderRadius: "50%",
                      }}
                    >
                      <i className="fa fa-facebook"></i>
                    </div>
                    &nbsp; Griya Manik
                  </div>
                  <div style={{ display: "flex", alignItems: "center" }}>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        backgroundColor: "rgba(0,0,0,0)",
                        border: "1px solid white",
                        width: "20px",
                        height: "20px",
                        padding: "15px",
                        borderRadius: "50%",
                      }}
                    >
                      <i className="fa fa-twitter"></i>
                    </div>
                    &nbsp; @Griya_Manik
                  </div>
                  <div style={{ display: "flex", alignItems: "center" }}>
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        backgroundColor: "rgba(0,0,0,0)",
                        border: "1px solid white",
                        width: "20px",
                        height: "20px",
                        padding: "15px",
                        borderRadius: "50%",
                      }}
                    >
                      <i className="fa fa-instagram"></i>
                    </div>
                    &nbsp; @Griya_Manik
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
    );
  }
}

export default footer;
