import React, { Component } from "react";
import Logo from "../component/logo";

class kontak extends Component {
  render() {
    return (
      <div className="container row">
        <Logo title="Kontak" />
        <div
          style={{
            width: "100%",
            border: "1px solid grey",
            borderRadius: "20px",
            marginTop: "50px",
            marginBottom: "50px",
          }}
        >
          <table>
            <tbody>
              <tr>
                <td>
                  <i
                    className="fa fa-2x fa-map-marker"
                    style={{ marginLeft: "20px", color: "#039be5" }}
                  ></i>
                </td>
                <td>
                  <b>Alamat</b>
                </td>
                <td>Jl. Basuki Rahmat Jombang Jawa Timur</td>
              </tr>
              <tr>
                <td>
                  <i
                    className="fa fa-2x fa-phone"
                    style={{ marginLeft: "20px", color: "#039be5" }}
                  ></i>
                </td>
                <td>
                  <b>Nomor Telepon</b>
                </td>
                <td>+62 818 8888 888</td>
              </tr>
              <tr>
                <td>
                  <i
                    className="fa fa-2x fa-envelope"
                    style={{ marginLeft: "20px", color: "#039be5" }}
                  ></i>
                </td>
                <td>
                  <b>Email</b>
                </td>
                <td>info@griyamanik.com</td>
              </tr>
              <tr>
                <td>
                  <i
                    className="fa fa-2x fa-globe"
                    style={{ marginLeft: "20px", color: "#039be5" }}
                  ></i>
                </td>
                <td>
                  <b>Website</b>
                </td>
                <td>www.griyamanik.com</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default kontak;
