import React from "react";
import Logo2 from "../assets/img/logo.png";

const logo = (props) => {
  const color = props.color ? props.color : "blue";
  const title = props.title;
  return (
    <div>
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
        }}
      >
        <img src={Logo2} alt="logo" height="150px" />
        <div
          className={`btn ${color}`}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            fontWeight: 600,
            width: "100%",
            fontSize: "2em",
            height: "50px",
            borderRadius: "10px",
          }}
        >
          {title}
        </div>
      </div>
    </div>
  );
};

export default logo;
